.landing-header {
    background-color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
}

.landing-header div {
    flex-grow: 1;
}

.header-title {
    display: flex;
    justify-content: left;
    padding: 20px;
}

.header-buttons {
    display: flex;
    justify-content: right;
    padding: 10px;
}

.header-signup {
    font-size: 25px;
    background-color: rgb(217, 184, 0);
    border: none;
    border-radius: 10px;
    margin: 10px;
    padding: 5px;
    cursor: pointer;
}

.header-signin {
    font-size: 25px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    border-color: lightgray;
    margin: 10px;
    padding: 5px;
    cursor: pointer;
}

.landing-header h1 {
    padding: 0;
    margin: 0;
    color: rgb(74, 54, 163);
}

.landing-body {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
}

.body-centered-main {
    display: flex;
    width: 70%;
    background-image: url("../resources/LandingBackground1.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin: 40px;
    justify-content: center;
    border-radius: 17px;
}

.inner-text {
    color: white;
    padding: 50px;
    width: 40%;
}

hr {
    border: none;
}